//
.project-wrap {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 20px 0; }
.portfolio-thumbnail {
  transition: all 0.2s ease-in-out;
  max-width: 250px;
  a {
    cursor: pointer;
    display: flex;
    img {
      width: 100%;
      height: auto; }
    img:hover {
      opacity: 0.5; } } }

@media screen and (max-width: 500px) {
  .portfolio-thumbnail {
    max-width: 160px; } }

//
.sketchbook-container {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  img {
    margin: 10px;
    width: 400px;
    height: auto; } }
