// Removing basic elements
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  transition: none; }

// MAIN HEADER
.nav-header {
  // Header positioning
  position: fixed;
  top: 0;
  left: 0;
  width: $nav-width;
  height: 100%;
  //
  background-color: $nav-color;
  overflow: hidden;
  // transitions
  transition: width 0.1s;
  /* WEBSITE LOGO */
  .nav-logo {
    display: flex;
    justify-content: center;
    padding-top: 50px;
    margin: 10px;
    img {
      width: 70px;
      height: auto; } } }
/* NAVIGATION BAR */
.nav-bar {
  display: flex;
  margin-left: 20px;
  margin-top: 50px;
  /* NAVIGATION LIST */
  .nav-links {
    li {
      list-style: none;
      padding-top: 10px;
      a {
        line-height: 30px;
        color: $text-color;
        letter-spacing: 3px; }
      a:hover {
        color: darken($text-color, 20%); } } } }
//
ul.dropdown-menu {
  li {
    padding-left: 1.5em;
    font-size: 80%;
    //
    display: none; } }
// hover to show dropdown
html.dropdown-active {
  ul.dropdown-menu {
    li {
      display: block; } } }
// Portfolio dropdown
.portfolio-dropdown {
  cursor: pointer; }

// Mobile nav icon
.nav-icon-open-wrapper {
  display: none;
  cursor: pointer; }
.nav-icon-open-wrapper {
  div {
    height: 4px;
    margin: 5px;
    width: 27px;
    background-color: $text-color;
    transition: all 0.3s ease; } }
.toggle .line1 {
  transform: translate(0px, 9px) rotate(45deg); }
.toggle .line2 {
  opacity: 0; }
.toggle .line3 {
  transform: translate(-0px, -9px) rotate(-45deg); }

.nav-body-overlay {
  z-index: 100;
  transition: opacity 0.2s ease-in-out, transform 0s ease-in-out 0.2s;
  display: block;
  position: fixed;
  left: 0;
  width: 100%;
  height: 100%;
  background: #00000099;
  opacity: 0;
  transform: translateX(100%); }

// Switch to mobile navigation
@media screen and (max-width: 850px) {
  .nav-header {
    width: 200px; } }
@media screen and (max-width: $switch-to-mobile), screen and (max-height: 425px) {
  .nav-header {
    z-index: 100;
    transition: none;
    // Top header
    width: 100%;
    height: auto;
    justify-content: center;
    .nav-logo {
      padding-top: 0; } }
  .nav-bar {
    margin: auto;
    .nav-links {
      z-index: 101;
      position: fixed;
      height: 100vh;
      background: $nav-color;
      width: 60%;
      transform: translateX(-100%);
      li {
        padding-left: 30px; } } }
  .nav-icon-open-wrapper {
    display: block;
    position: fixed;
    top: 1.5em;
    left: 1.5em; }
  html.nav-active {
    body {
      overflow: hidden;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0; }
    .nav-body-overlay {
      opacity: 1;
      transition: opacity 0.2s ease-in-out, transform 0s;
      transform: translateX(0); }
    .nav-links {
      opacity: 1;
      transition: all 0.3s ease;
      transform: translateX(0); } } }
